<template>
   <v-card class="mx-auto">
    <v-card
        class="mx-auto"
        color="black"
        dark
    >
        <v-card-title >
            <span class="title font-weight-bold" style="font-size:100px">basic infomation</span>
        </v-card-title>


        <v-container>
            <v-row>
                <v-col>
                    hostname:     {{basicInform.hostname}},<br>
                    mac_address:  {{basicInform.mac_address}},<br>
                </v-col>
                <v-col>
                    vendor:       {{basicInform.vendor}},<br>
                    delay:        {{basicInform.delay}},<br>
                </v-col>
            </v-row>
        </v-container>
    </v-card>

    <v-divider></v-divider>

    <v-data-table
    :headers="headers"
    :items="desserts"
    :items-per-page="5"
    class="elevation-1"
  >
        <template v-slot:item.actions="{ item }">
            <v-icon
                small
                class="mr-2"
                @click="checkPort(item)"
            >
                mdi-eye
            </v-icon>
        </template>


        <template v-slot:top>
            <v-toolbar flat color="white">
                <!-- <v-toolbar-title>My CRUD</v-toolbar-title> -->
                <v-card-title>
                    <span class="title font-weight-bold">appInform</span>
                </v-card-title>

                <v-divider
                class="mx-4"
                inset
                vertical
                ></v-divider>

                <v-spacer></v-spacer>

                
                
            </v-toolbar>
        </template>

        
        <template v-slot:no-data>
            <v-btn color="primary" @click="initialize">Reset</v-btn>
        </template>

    </v-data-table>

     <v-divider></v-divider>

    <v-card>
        <v-card-title>
            <span class="title font-weight-bold">webInform</span>
        </v-card-title>

        <v-tabs
        v-model="tab"
        background-color="black"
        dark
        >
            <v-tab
                v-for="item in webInform"
                :key="item"
            >
                port:{{ item.port }}
            </v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
            <v-tab-item
                v-for="item in webInform"
                :key="item"
            >
                <v-container>
                    <v-row>
                        <v-col>
                            cdn:{{item.cdn}}<br>
                            cms:{{item.cms}}<br>
                            framework:{{item.framework}}<br>
                            frontend:{{item.frontend}}<br>
                        </v-col>
                        <v-col>
                            lang:{{item.lang}},<br>
                            server:{{item.server}},<br>
                            system:{{item.system}},<br>
                            waf:{{item.waf}}<br>
                        </v-col>
                    </v-row>
                </v-container> 
            </v-tab-item>
        </v-tabs-items>
    </v-card>

  </v-card>

  
</template>

<script>
import {  } from "../network/request.js"

export default {
  name: "ipInfo",
  data() {
    return {
        tab: null,
        dialog: false,

        //基本物理信息 basicInform
        // basicInform: this.$store.state.basicInform,
        basicInform: [],

        //web应用信息 webInform
        // webInform:this.$store.state.webInform,
        webInform:[],

        //端口对应的服务信息 appInform

        //port表头
        // headers: this.$store.state.headers,
        headers:[],

        //scan result表头
        // headers1: this.$store.state.headers1,
        headers1:[],

        //port表 内容
        desserts: [],

        //result表 内容
        // desserts1: this.$store.state.desserts1,
        desserts1:[],



        editedIndex: -1,

        //当前选中，供查看的port
        editedItem: {
            port:0,
            service:"",
            version:""
        },
        
        defaultItem: {
            port:0,
            service:"",
            version:""
        },
        
        
    };
  },

  computed: {
      
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
    //   this.initialize()
        this.basicInform = this.$store.state.basicInform
        this.webInform = this.$store.state.webInform

        this.headers = this.$store.state.headers
        this.headers1 = this.$store.state.headers1

        this.desserts = this.$store.state.desserts
        this.desserts1 = this.$store.state.desserts1

        // this.desserts = this.$store.state.desserts
    },

    methods: {
    //   initialize () {
    //     this.desserts = this.$store.state.desserts
    //   },

      checkPort(item) {
        // console.log(item)
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)


        this.dialog = true
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

    },
}

</script>


<style lang="less">

</style>