import Vue from 'vue'
import VueRouter from 'vue-router'

import topo from '../views/topo.vue'
import search from '../views/search.vue'

import ipInfo from '../components/ipInfo.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'search',
    component: search
  },
  {
    path: '/topo',
    name: 'topo',
    component: topo,
    
    children:[
      {
        path: '/ipInfo',
        name: 'ipInfo',
        component: ipInfo
      }
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
